@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  margin: 0;
  font-family: 'Montserrat', BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #232526;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  

}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px transparent; 
  border-radius: 50%;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(172, 13, 13); 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #b30000; 
}



  .TodoList{
     background: #283048;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
     background: linear-gradient(to right, #859398, #283048); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
     padding: 2px;
     margin: 10px;     
     display: -webkit-flex;     
     display: flex;
     margin-left: auto;
     margin-right: auto;
     color: snow;
     -webkit-transform: skew(20deg);
             transform: skew(20deg);
     text-align: center;
     width: 80%;
     cursor: pointer;
     transition: all 0.5s ease-in-out;
}
.TodoList:hover{
     -webkit-filter: brightness(140%) contrast(90%);
             filter: brightness(140%) contrast(90%);
}

.todo-heading{
     -webkit-flex: 1 1;
             flex: 1 1;
     -webkit-transform: skew(-20deg);
             transform: skew(-20deg);
     letter-spacing: 2px;
     display: inline-block;
}

.todo-edit{
     cursor: pointer;
     color: rgb(24, 153, 24);
}


.TodoList .todo-edit:active,
.TodoList .todo-edit:hover,
.TodoList .todo-edit:focus{
     outline: none;
     border: none;
}

.todo-delete{
 position: absolute;
 top: 0;
 bottom:0;
 left: 0;
 opacity: 0;
 color: rgb(212, 31, 31);
 transition: all 0.5s;
 cursor: pointer;
}
.todo-complete{
     text-decoration: line-through;
     font-size: 300;
     -webkit-filter: brightness(70%);
             filter: brightness(70%);
}

.TodoList:hover .todo-delete{
     opacity: 1;
     -webkit-animation-name: example;
             animation-name: example;
     -webkit-animation-duration: 2s;
             animation-duration: 2s;
}
@-webkit-keyframes example {
     0%   { left:-20px; }
     100% { left:0px; }
}
@keyframes example {
     0%   { left:-20px; }
     100% { left:0px; }
}

.movement{
     width: 50px;
     height: 50px;
     border-radius: 100%;
     position: absolute;
     left: 30px;
     z-index: -2;
     background-color: white;
     -webkit-animation: bounce 10s infinite;
             animation: bounce 10s infinite;
}
.movement2{
     width: 50px;
     height: 50px;
     border-radius: 100%;
     position: absolute;
     right: 30px;
     z-index: -2;
     background-color: white;
     -webkit-animation: bounce2 10s infinite;
             animation: bounce2 10s infinite;
}
@-webkit-keyframes bounce{
     0% {top: 10%}
     50% {top: 90%}
     100% {top: 10%}
}
@keyframes bounce{
     0% {top: 10%}
     50% {top: 90%}
     100% {top: 10%}
}

@-webkit-keyframes bounce2{
     0% {top: 90%}
     50% {top: 10%}
     100% {top: 90%}
}

@keyframes bounce2{
     0% {top: 90%}
     50% {top: 10%}
     100% {top: 90%}
}



@media only screen and (min-width: 500px) {
     .TodoList {
       width: 50%;
     }
}




